import { useMemo } from 'react'
import {
  getAvailabilityReasonForConsole,
  useIsAvailableDebounced,
  UseIsNameAvailable,
} from '@aserto/console-common'

import { useIsAuthorizerInstanceNameAvailable } from '../../api/internal'

const useIsAuthorizerNameAvailable = (): UseIsNameAvailable =>
  useMemo(() => {
    const useIsAuthorizerNameAvailableInternal = (name: string, defaultName: string) => {
      const queryResult = useIsAuthorizerInstanceNameAvailable(name, {
        enabled: name !== defaultName,
      })

      const response = useMemo(() => {
        const isAvailable =
          !queryResult?.data?.availability ||
          queryResult?.data?.availability === 'NAME_AVAILABILITY_AVAILABLE'

        return {
          isAvailable: isAvailable,
          reason: getAvailabilityReasonForConsole(queryResult?.data?.reason),
        }
      }, [queryResult?.data])

      return response
    }

    const useIsAuthorizerNameAvailableDebounced = (key: string, defaultkey: string) => {
      const useRunTarget = () => useIsAvailableDebounced(useIsAuthorizerNameAvailableInternal)
      const run = useMemo(useRunTarget, [])
      return run(key, defaultkey)
    }
    return useIsAuthorizerNameAvailableDebounced
  }, [])

export default useIsAuthorizerNameAvailable
