import React, { useMemo, useState } from 'react'
import { Button, PaddedContent, PageHeader, PageHeaderContent } from '@aserto/console-common'

import { useAuthorizerInstances } from '../../api/internal'
import { AuthorizerCard } from '../../components/authorizers/AuthorizerCard'
import AuthorizerModal from '../../components/authorizers/AuthorizerModal'
import { CardGrid, Header } from '../PolicyInstance/styles'

const Authorizers: React.FC = () => {
  const { data } = useAuthorizerInstances()

  const authorizerInstances = useMemo(() => {
    return data?.results || []
  }, [data?.results])

  const [showAddAuthorizerModal, setShowAddAuthorizerModal] = useState(false)
  const onClickAddAuthorizer = () => {
    setShowAddAuthorizerModal(true)
  }

  return (
    <>
      <Header>
        <PageHeader hasBorderBottom mobileBreakpoint={912} title="Authorizers">
          <PageHeaderContent>
            <AuthorizerModal
              show={showAddAuthorizerModal}
              onHide={() => setShowAddAuthorizerModal(false)}
            />
            <Button
              data-testid="add-authorizer-btn"
              id="add-a-authorizer"
              onClick={onClickAddAuthorizer}
            >
              Connect an Authorizer
            </Button>
          </PageHeaderContent>
        </PageHeader>
      </Header>
      <PaddedContent paddingTop={140}>
        <CardGrid>
          {authorizerInstances!.map(function (data) {
            return (
              <AuthorizerCard
                key={data.name}
                fullHeight={true}
                text={data.url}
                title={data.name}
              ></AuthorizerCard>
            )
          })}
        </CardGrid>
      </PaddedContent>
    </>
  )
}

export default Authorizers
