import React, { useState } from 'react'
import { Button, CardModal, useShowError, ValidatedInput } from '@aserto/console-common'

import { AuthorizerInstance } from '../../../../common/types'
import { useAddAuthorizerInstance } from '../../../api/internal'
import useIsAuthorizerNameAvailable from '../../../lib/availability/UseIsAuthorizerInstanceNameAvailable'
import AsertoNameValidator from '../../../lib/validation/AsertoNameValidator'
import { ButtonsContainer, ContentContainer, FieldContainer, FormContainer } from './styles'

export type AuthorizerModalProps = {
  authorizerInstance?: AuthorizerInstance
  show: boolean
  onSuccess?: () => void
  onHide: () => void
}

const AuthorizerModal: React.FC<AuthorizerModalProps> = ({ authorizerInstance, show, onHide }) => {
  const [instanceName, setInstanceName] = useState('')
  const [instanceUrl, setInstanceUrl] = useState('')
  const [apiKey, setApiKey] = useState('')
  const clearModalState = () => {
    setInstanceName('')
    setInstanceUrl('')
    setApiKey('')
  }
  const showError = useShowError()

  const { mutate: addAuthorizerInstace } = useAddAuthorizerInstance({
    onError: (e) => {
      showError(e)
    },
  })

  return (
    <>
      <CardModal
        cardHeight="100%"
        show={show}
        title="Connect an authorizer"
        onHide={() => {
          clearModalState()
          onHide()
        }}
      >
        <ContentContainer className="add-a-authorizer-modal-content">
          <div>Provide a unique name and click the Add button.</div>
          <FormContainer>
            <FieldContainer>
              <ValidatedInput
                autoFocus
                defaultValue={authorizerInstance?.name}
                disabled={!!authorizerInstance?.name}
                label="Instance Name"
                useIsAvailable={useIsAuthorizerNameAvailable()}
                validator={AsertoNameValidator}
                value={authorizerInstance?.name}
                onChange={(v) => setInstanceName(v || '')}
              />
            </FieldContainer>
            <FieldContainer>
              <ValidatedInput
                label="Instance URL"
                value={authorizerInstance?.url}
                onChange={(v) => setInstanceUrl(v || '')}
              />
            </FieldContainer>
            <FieldContainer>
              <ValidatedInput
                label="API Key (Optional)"
                type="password"
                value={authorizerInstance?.apiKey}
                onChange={(v) => setApiKey(v || '')}
              />
            </FieldContainer>
          </FormContainer>
          <ButtonsContainer>
            <Button
              data-testid="cancel-btn"
              variant="secondary"
              onClick={() => {
                clearModalState()
                onHide()
              }}
            >
              Cancel
            </Button>
            <Button
              data-testid="add-authorizer-modal-btn"
              disabled={instanceName === '' || instanceUrl === ''}
              id="add-authorizer"
              type="submit"
              onClick={() => {
                onHide()
                addAuthorizerInstace({ name: instanceName, url: instanceUrl, apiKey: apiKey })
              }}
            >
              Save
            </Button>
          </ButtonsContainer>
        </ContentContainer>
      </CardModal>
    </>
  )
}

export default AuthorizerModal
